.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav-link {
    text-decoration: none;
    margin-left: 10px;
    margin-right: 10px;
    color: rgb(23, 29, 39);
    padding: 8px;
    background-color: #ffe17f;
    border-radius: 20px;
}
.nav-link:hover {
    color:rgb(39, 121, 160);
    background: #ffe065;
    transform: scale(1.1);
    transition: all ease .2s;
}